import React from 'react';
import styles from 'src/styles/Header.module.css';
import Core from 'src/Core/Core.js';
import Config from 'src/Config.js';
import { Link } from 'gatsby';

class HeaderInfo extends React.Component {
    click() {
        document.location.href = Core.path('/');
    };

    render() {
        return (
            <div className={styles.info} onClick={this.click}>
                <div className={styles.iconWrapper}>
                    <img src={Config.ProjectData.AvatarURL} alt="" className={styles.icon} />
                </div>
                <h1 className={styles.name}>
                    {Config.ProjectData.Name}
                </h1>
            </div>
        );
    };
};

class HeaderContent extends React.Component {
    constructor(props) {
        super(props);

        this.click = this.click.bind(this);

        this.state = {};

        this.state.state = null;

        this.state.state = 'close';
    };

    click() {
        if (this.state.state === 'close') {
            this.setState({
                state: 'open'
            });
        } else if (this.state.state === 'open') {
            this.setState({
                state: 'close'
            });
        };
    };

    render() {
        return (
            <>
                <div className={styles.content}>
                    <nav>
                        {this.props.children}
                    </nav>
                </div>
                <div className={styles.contentMobile}>
                    <div className={styles.contentMobileBtn} onClick={this.click} state={this.state.state}>
                        <div className={styles.one}></div>
                        <div className={styles.two}></div>
                        <div className={styles.three}></div>
                    </div>
                    <div className={styles.contentMobileContent} state={this.state.state}>
                        <nav>
                            {this.props.children}
                        </nav>
                    </div>
                </div>
            </>
        );
    };
};

class HeaderContentItem extends React.Component {
    render() {
        return (
            <Link className={styles.e} to={this.props.href} title={this.props.title}>
                <h1>
                    {this.props.children}
                </h1>
            </Link>
        );
    };
};

class Header extends React.Component {
    render() {
        return (
            <div className={styles.Header}>
                <HeaderInfo />
                <HeaderContent>
                    <HeaderContentItem href="/" title="Главная страница">
                        Главная
                    </HeaderContentItem>
                    <a className={styles.e} href="https://kanami-community.github.io/docs/" title="Команды бота">
                        <h1>
                            Команды
                        </h1>
                    </a>
                    <HeaderContentItem href="/developers/" title="Разработчики">
                        Devs
                    </HeaderContentItem>
                    <HeaderContentItem href="/features/" title="Возможности бота">
                        Возможности
                    </HeaderContentItem>
                </HeaderContent>
            </div>
        );
    };
};

export default Header;