import Config from 'src/Config.js';

class CoreObject {
    constructor() {
        let Core = {};

        // Path
        function path(path) {
            let url = `${Config.PathPrefix}${path}`;

            function t(url) {
                url = url.replace('//', '/');

                return url;
            };

            url = t(url);

            return url;
        };

        Core.path = path;

        return Core;
    };
};

const Core = new CoreObject();

export default Core;